/* modules */
.helps {
  height: 900px;
  background: linear-gradient(to left, #87fc70, #0bd318);
  padding: 0;
  color: $color-white;
  overflow: visible;
  position: relative;
  // display: none
  &__wrapper {
    height: 100%;
    overflow: hidden;
    p {
      top: -20px;
      opacity: 0; } }

  .container {
    position: relative;
    height: 100%; }
  .h1 {
    color: $color-white;
    opacity: 0;
    top: -20px; }

  &__layer {
    overflow: hidden;
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin-top: -10px;
    .layer {
      position: absolute;
      top: 0;
      opacity: 0;
      &--1 {
        left: -564px !important;
        top: 147px !important; }
      &--2 {
        top: inherit !important;
        right: -60px !important;
        bottom: 139px !important;
        left: inherit !important; }
      &--3 {
        top: 240px !important;
        right: -340px !important;
        left: inherit !important; }
      &--4 {
        left: 343px !important;
        top: 100px !important; }
      &--5 {
        left: 485px !important;
        top: 800px !important;
 } }        // bottom: -70px !important

    &--last {
      overflow: inherit; } }

  .applinks {
    opacity: 0;
    top: -20px; } }

.app {
  &icon {
    margin-top: 268px;
    filter: drop-shadow(0 0 60px rgba(#045309, 0.3));
    margin-bottom: 65px;
    display: inline-block;
    opacity: 0;
    top: -20px; }

  &links {
    &>a {
      display: inline-block;
      &+a {
        margin-left: 30px; } } } }

