/**/
.properties {
  padding-top: 90px;
  padding-bottom: 24px;
  // display: none

  .row {
    display: flex; }

  &__round {
    height: 468px;
    width: 468px;
    border-radius: 50%;
    border: 1px solid #dcdcdc;
    margin-left: 55px;
    position: absolute;
    margin-top: 130px;
    opacity: 0;
    // z-index: 10
    &::after, &::before {
      position: absolute;
      left: 50%;
      top: 0;
      height: 100%;
      width: 1px;
      background-color: pink;
      // content: ''
      z-index: 100; }
    &::before {
      height: 1px;
      width: 100%;
      left: 0;
      top: 50%; }

    &__block {
      height: 60px;
      width: 60px;
      border-radius: 50%;
      background-color: #dcdcdc;
      box-shadow: 0 0 0 5px $color-white;
      text-align: center;
      line-height: 60px;
      position: absolute;
      cursor: pointer;
      color: $color-white;
      transform: scale(1) translate(-50%, -50%);
      transform-origin: 0 0;
      right: 85px;
      top: 20px;
      z-index: 100;
      transition: all 0.1s linear;
      opacity: 0;
      &.is-active {
        background: linear-gradient(to right, #2cde2f, #66f159);
        transform: scale(1.45) translate(-50%, -50%);
        box-shadow: 0 0 0 3px $color-white, 0 0 30px rgba(#045309, 0.3); }

      &--2 {
        right: -20px;
        top: 105px; }

      &--3 {
        top: 50%;
        right: -60px; }

      &--4 {
        top: 360px;
        right: -20px; }

      &--5 {
        top: 445px;
        right: 85px; }

      svg {
        fill: $color-white;
        vertical-align: -8px;
        display: inline-block; } } }

  &__text {
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
    padding-left: 20px;
    opacity: 0;
    .h2, h2 {
      transform: translateY(-10px);
      transition: all 1s ease; }
    p {
      margin: 0;
      transform: translateY(-10px);
      transition: all 1s ease; }
    &.is-active {
      opacity: 1;
      .h2, h2, p {
        transform: translateY(0); } } } }

