/**/
.iphone3 {
  position: relative;
  width: 800px;
  height: 550px;
  margin: 0 auto;
  // background: pink
  img {
    position: absolute;
    // top: 100%
    opacity: 0;
    z-index: 10;
    left: -25px;
    // transform: translateY(100%)
    &:first-child {
      margin-top: 100px; }
    &:nth-child(2) {
      left: 50%;
      transform: translateX(-50%);
      margin-left: 12px;
      z-index: 50; }
    &:last-child {
      right: -45px;
      left: inherit;
      margin-top: 100px; } } }

